// pages/api/focus-log.ts
import { PrismaClient } from "@prisma/client";
import type { NextApiRequest, NextApiResponse } from "next";

const prisma = new PrismaClient();

export default async function handler(req: NextApiRequest, res: NextApiResponse) {
  const userId = parseInt(req.query.userId as string || req.body.userId); // allow fallback

  if (req.method === "POST") {
    const { keyboard, mouseClicks, mouseDistance, focusScore } = req.body;

    try {
      const log = await prisma.focusLog.create({
        data: {
          keyboard,
          mouseClicks,
          mouseDistance,
          focusScore,
          userId,
        },
      });

      console.log("✅ Focus log saved:", log);
      return res.status(201).json(log);
    } catch (err) {
      console.error("🔥 Prisma error:", err); // ✅ 에러 상세 로그 확인
      return res.status(500).json({ error: "Failed to save focus log" });
    }
  }

  if (req.method === "GET") {
    try {
      const logs = await prisma.focusLog.findMany({
        where: { userId },
        orderBy: { timestamp: "desc" },
        take: 10,
      });
      return res.status(200).json(logs);
    } catch (err) {
      console.error("🔥 Prisma fetch error:", err);
      return res.status(500).json({ error: "Failed to fetch focus logs" });
    }
  }

  res.status(405).json({ error: "Method not allowed" });
}
